@import "../../../../../../../Assets/colorPallete/colorPallete.scss";


.InputFields {
  width: 100%; 
  height: auto;
  border-radius: 10px;
  border: 2px solid lightgrey; 
  background-color: $color-1;
  
  .textField{
      width: 100%;
      height: auto;
      padding-left: 0.3em;
  }


}

.InputFieldsError{
    width: 100%; 
    height: auto;
    border-radius: 10px;
    border: 1px solid red; 
    background-color: $color-1;
    
    .textField{
        width: 100%;
        height: auto;
        padding-left: 0.3em;
    }
  
}

.error {
    color:red;
    font-size: 0.5vw;
}