.root {
    width: auto;
    height: auto;
    display: block;

    .renderSuggestion {
        position: relative;
        width: 100%;
        max-height: 15vh;
        z-index: 1000;

        .list {
            width: 100%;
            max-height: 15vh;
            position: absolute;
            display: flex;
            flex-direction: column;
            list-style-type: none;
            list-style: none;
            text-align: left;
            overflow-y: scroll;
            border: 1px solid lightgray;
            background-color: white;
            transform: translateY(-10%);
            padding-left: 1em;
            z-index: 1000;
            color: #000000;

            > li {
                cursor: pointer;
                -webkit-touch-callout: none;
                -webkit-user-select: none;
                -khtml-user-select: none;
                -moz-user-select: none;
                -ms-user-select: none;
                user-select: none;
                z-index: 10;
            }

            > li:hover {
                font-weight: bolder;
            }
        }
    }
}
