.facilityAddressFields {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    > div {
        margin-right: 1.2em;
        width: 15%;
    }
    > div:nth-child(1) {
        width: 36%;
    }
    > div:nth-child(2) {
        width: 30%;
    }
    > div:nth-child(3) {
        width: 25%;
    }
    > div:nth-child(4) {
        width: 25%;
    }
    > div:nth-child(5) {
        width: 25%;
    }
    > div:nth-child(6) {
        width: 15%;
    }
}
