@import "../../../../../../../Assets/colorPallete/colorPallete.scss";


.InputFields {
    width: 100%; 
    height: auto;
    border-radius: 10px;
    border: 0px solid lightgrey; 
    background-color: $color-1;
    
    .textField{
        width: 100%; 
        height: auto;
        border-radius: 10px;
        border: 2px solid lightgrey; 
        background-color: $color-1;
        padding-left: 0.3em;
        padding-top: 0.65em;
        padding-bottom: 0.65em;
    }
  
}


.InputFieldsError{
    width: 100%; 
    height: auto;
    border-radius: 10px;
    border: 0px solid red; 
    background-color: $color-1;
    
    .textField{
        width: 100%; 
        height: auto;
        border-radius: 10px;
        border: 2px solid red; 
        background-color: $color-1;
        padding-left: 0.3em;
        padding-top: 0.65em;
        padding-bottom: 0.65em;
    }
}

.error {
    color:red;
    font-size: 0.5vw;
}