@import "../../../../../../Assets/colorPallete/colorPallete.scss";

.root {
    display: flex;
    flex-direction: column;
    width: 100%;

.viewTable{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: auto;

    .TableHeadRoot{
        display: inline-flex;
        width: 100%;
        height: auto;
       

        .tableHeadRow {
            display: inline-flex;
            width: 100%;
            height: auto;
            background-color: $color-2;
            
            .tableCell{
                display: inline-flex;
                width: 100%;
                height: auto;
                -webkit-touch-callout: none;
                -webkit-user-select: none; 
                 -khtml-user-select: none; 
                   -moz-user-select: none; 
                    -ms-user-select: none; 
                        user-select: none; 
                                              

                .noTextField {
                    height: auto;
                    padding: 0;
                }
                .withTextfield {
                    padding: 0;

                    height: auto;

                    .showIcon {
                        display: inline-block;
                        position: absolute;

                        .sortIcon {
                            margin-left: 2em;
                            display: inline-block;

                            & :hover {
                                cursor: pointer;
                            }
                        }
                    }
                }

        
            }

            .tableCell:nth-child(1){
                width: 12%;
                display: inline-block;
       
            }

            .tableCell:nth-child(2){
                width: 2%;
                display: inline-block;
           
            }

            .tableCell:nth-child(3){
                width:18%;
                display: inline-block;
       
            }

            .tableCell:nth-child(4){
                width: 22%;
                display: inline-block;
           
            }

            .tableCell:nth-child(5){
                width: 13%;
                display: inline-block;
       
            }

            .tableCell:nth-child(6){
                width: 13%;
                display: inline-block;
     
            }

            .tableCell:nth-child(7){
                width: 19.2%;
                display: inline-block;
          
            }



        }
    }

    .tableBody{
        width: 100%;
        height: 50vh;
        display: flex;
        flex-direction: column;
        position: relative;
        overflow-y: scroll;

        .tableRow{
            color: $color-3;
            width: 100%;
            height: auto;
            display: flex;
            flex-direction: row;
          

            .tableCell{
                display: inline-flex;
                width: 100%;
                height: auto;
                flex-direction: row;
                -webkit-touch-callout: none;
                -webkit-user-select: none; 
                 -khtml-user-select: none; 
                   -moz-user-select: none; 
                    -ms-user-select: none; 
                        user-select: none; 
                
            }





            .tableCell:nth-child(1){
                width: 12.1%;
                display: inline-block;
                overflow-wrap: break-word;
               
            }

            .tableCell:nth-child(2){
                width: 2%;
                display: inline-flex;
                justify-content: center;
                overflow-wrap: break-word;
                 
            }

            .tableCell:nth-child(3){
                width:18.2%;
                display: inline-block;
                overflow-wrap: break-word;
             
            }

            .tableCell:nth-child(4){
                width: 22.2%;
                display: inline-block;
                overflow-wrap: break-word;
          
            }

            .tableCell:nth-child(5){
                width: 13.1%;
                display: inline-block;
                overflow-wrap: break-word;
           
            }

            .tableCell:nth-child(6){
                width: 13.1%;
                display: inline-block;
                overflow-wrap: break-word;
         
            }

            .tableCell:nth-child(7){
                width: 19.33%;
                display: inline-block;
                overflow-wrap: break-word;
         
            }

        }

    .tableRow:nth-of-type(even){
            background-color: white;
        }
    .tableRow:nth-of-type(odd){
            background-color: $color-1;
        }

  
    }

}

.paginationDiv {
position: relative;
background-color: $color-4;
display: inline-flex;
flex-direction: row;
width: auto;
height: auto;
align-items: center;

.pageCount{
    margin-top: 1em;
display: inline-flex;
flex: 2;
justify-content: flex-start;
padding-left: 1em;
margin-bottom: 1em;
-webkit-touch-callout: none;
-webkit-user-select: none; 
 -khtml-user-select: none; 
   -moz-user-select: none; 
    -ms-user-select: none; 
        user-select: none; 

}

.Pagination{
    display: inline-flex;
    justify-content: flex-end;
    flex: 3;
    padding-right: 2em;
}

}

}